<template>
  <v-app dark>
    <v-navigation-drawer
      persistent
      :mini-variant="miniVariant"
      :clipped="clipped"
      v-model="drawer">
      <v-list>
        <v-list-tile
          :to="item.to"
          :key="i"
          v-for="(item, i) in items">
          <v-list-tile-action>
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title v-text="title" class="primary--text"></v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <main>
      <v-container fluid>
        <v-slide-y-transition mode="out-in">
          <router-view></router-view>
        </v-slide-y-transition>
      </v-container>
    </main>
    <v-footer class="blue darken-2" fixed>
      <v-layout row wrap align-center>
        <v-flex xs12>
          <div class="white--text ml-3">
             © Addiction {{ new Date().getFullYear() }}
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        clipped: true,
        drawer: false,
        miniVariant: false,
        items: [
          {icon: 'home', title: '', to: '/home'},
          {icon: 'info', title: '', to: '/about'}
        ],
        title: 'Versatil'
      }
    }
  }
</script>

<style lang="stylus">
  @import 'stylus/main.styl'
</style>



